import React from "react";
import { Routes, Route, Navigate } from "react-router-dom";

const permission = false;

const AdminPage = () => {
  return (
    <Routes>
      {permission ? (
        <Route path="/admin" />
      ) : (
        <Route element={<Navigate to="/login" />} />
      )}

      {/* <Route
        render={() =>
          permission ? <h3>Panel admina</h3> : <Navigate to="/login" />
        }
      /> */}
    </Routes>
  );
};

export default AdminPage;
